@use '../abstracts/' as *;
@use './typography' as *;

*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  scroll-padding-top: 70px;
  // media queries
  @include respond(phone) {
    & {
      font-size: 50%;
    }
  }

  @include respond(tab-p) {
    & {
      font-size: 55%;
    }
  }

  @include respond(tab-l) {
    & {
      font-size: 60%;
    }
  }

  @include respond(big-desk) {
    & {
      font-size: 65%;
    }
  }
}

body {
  position: relative;
  background-color: var(--color-bg);
  box-sizing: border-box;
  font-family: var(--main-font);
  //   padding: 2rem;
  // height: 150rem;
  transition: background-color 0.15s;

  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

.separator {
  position: relative;
  width: 100vw;

  &:not(:last-child) {
    margin-bottom: 5rem;
  }
}
